import { BlogPost, BlogIndex, OpenPeepsImageConfig } from "@/types/blog";
import path from "path";
import matter from "gray-matter";
import { calculateReadingTime } from "./reading-time";
import fs from "fs";

const BLOG_POSTS_DIR = path.join(process.cwd(), "src/content/blog/posts");

const DEFAULT_IMAGE_CONFIG: OpenPeepsImageConfig = {
  name: "developer",
  skinColor: "F2D3B8",
  hairColor: "2C1810",
  shirtColor: "4f46e5",
  pantsColor: "1e293b",
  width: 1200,
  height: 630,
};

export async function getBlogPost(slug: string): Promise<BlogPost | null> {
  try {
    const filePath = path.join(BLOG_POSTS_DIR, `${slug}.mdx`);

    return {
      slug,
      title: data.title,
      description: data.description,
      date: data.date,
      author: data.author,
      tags: data.tags || [],
      content,
      readingTime: calculateReadingTime(content),
      category: data.category,
      image: {
        ...DEFAULT_IMAGE_CONFIG,
        ...data.image,
      },
      searchTerms: [
        data.title.toLowerCase(),
        data.description.toLowerCase(),
        ...(data.tags || []).map((tag: string) => tag.toLowerCase()),
        data.category.toLowerCase(),
      ],
    };
  } catch (error) {
    return null;
  }
}

export async function getAllBlogPosts(): Promise<BlogPost[]> {
  const files = fs.readdirSync(BLOG_POSTS_DIR);
  const posts = await Promise.all(
    files.map(async (filename) => {
      const filePath = path.join(BLOG_POSTS_DIR, filename);
      const source = fs.readFileSync(filePath, "utf8");
      const { data, content } = matter(source);

      return {
        slug: filename.replace(/\.mdx$/, ""),
        title: data.title,
        description: data.description,
        date: data.date,
        author: data.author,
        tags: data.tags || [],
        content,
        readingTime: calculateReadingTime(content),
        category: data.category,
        image: {
          ...DEFAULT_IMAGE_CONFIG,
          ...data.image,
        },
        searchTerms: [
          data.title.toLowerCase(),
          data.description.toLowerCase(),
          ...(data.tags || []).map((tag: string) => tag.toLowerCase()),
          data.category.toLowerCase(),
        ],
      };
    })
  );

  return posts.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
}
